import uniCard from '@/components/uni-card/uni-card.vue'
var graceChecker = require("../../common/graceChecker.js");
var _self;
var tempFilePaths;
var visibility;
var joinMethod;
export default {
	components: {
		uniCard
	},
	data() {
		return {
			"pickPic": "选择照片"
		}
	},
	onLoad: function(options) {
		console.log(options.id);
	},
	methods: {
		createGroup: function(e) {

			//定义表单规则
			const rule = [{
					name: "name",
					checkType: "string",
					checkRule: "1,10",
					errorMsg: "姓名应为1-10个字符"
				},
				{
					name: "description",
					checkType: "string",
					checkRule: "1,50",
					errorMsg: "请控制为1-50个字符"
				}
			];

			/**
			 * FormData:
			 {
				 name:String,
				 description:String,
				 visibility:String,
				 joinMethod:String,
				 location:String,
				 pic:String,
				 type:String,
				 owner:UUID
			 }
			 */

			//进行表单检查
			const formData = e.detail.value;
			console.log('form data:' + JSON.stringify(formData))
			const checkRes = graceChecker.check(formData, rule);

			if (checkRes && visibility !== undefined && joinMethod !== undefined) {
				uni.showToast({
					title: "验证通过!",
					icon: "none"
				});

				let data = e.detail.value;
				data.createdAt = new Date();
				data.createdBy = 1;
				data.visibility = visibility;
				data.joinMethod = joinMethod;
				data.owner = 'userId'
				console.log('create group for data: ' + JSON.stringify(data));

				uniCloud.uploadFile({
					filePath: tempFilePaths[0],
					onUploadProgress: function(progressEvent) {
						// console.log(progressEvent);
						var percentCompleted = Math.round(
							(progressEvent.loaded * 100) / progressEvent.total
						);
					},
					success(res) {
						console.log("successfully upload img " + res.fileID)
						data.imgId = res.fileID;

						uniCloud.callFunction({
								name: 'group-create',
								data: data
							})
							.then(res => {
								console.log("success with" + JSON.stringify(res));
								uni.redirectTo({
									url: 'group-view?id=' + res.result.id
								});
							});

					},
					fail(err) {
						console.error(err)
					},
					complete() {
						console.log("completed")
					}
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},
		upload: function() {
			_self = this;
			uni.chooseImage({
				count: 1,
				// sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				success: function(res) {
					tempFilePaths = res.tempFilePaths;
					console.log("Have chose image in path:" + tempFilePaths)
				}
			});
		},
		clickVisibilityCard(e) {
			visibility = e;

			uni.showToast({
				title: '点击卡片',
				icon: 'none'
			})
		},
		clickJoinCard(e) {
			joinMethod = e;

			uni.showToast({
				title: '点击卡片',
				icon: 'none'
			})
		}
	}
}
